      data: "between_bridges_1k.raw"
    },
    {
      name: "Between Bridges (2K)",
      data: "between_bridges_2k.raw"
    },
    {
      name: "Between Bridges (4K)",
      data: "between_bridges_4k.raw"
    },
    {
